import { buildNewFormBuscarPersonas, IFormBuscarPersonas } from './../crud.store.model';
import update from 'immutability-helper';
import { AutorizacionService } from './../../../../services/autorizacion.service';
import { IGridPersonas } from '../crud.store.model';
import { IDataGridPageRequest } from '@siu/shared';

export class CrudGridPersonasActions {

  constructor(
    private getState: () => IGridPersonas,
    private setState: (newState: IGridPersonas) => void,
    public autorizacionService: AutorizacionService) {
  }

  fetchPersonsBegin = () => {
    const state = this.getState();
    this.setState({ ...state, isLoading: true, error: null });
  };

  fetchPersonsSuccess = (items, total, page, pageSize, orderBy, orderByDir) => {
    const state = this.getState();

    const new_state = update(state, {
      isLoading: { $set: false },
      source: {
        items: { $set: items },
        total: { $set: total },
        page: { $set: page },
        pageSize: { $set: pageSize },
        orderBy: { $set: orderBy },
        orderDir: { $set: orderByDir }
      },
    });

    this.setState(new_state);
  };

  fetchPersonsError = (error: any) => {
    const state = this.getState();
    const new_state = update(state, {
      isLoading: { $set: false },
      error: { $set: error },
      source: { items: { $set: [] } }
    });

    this.setState(new_state);
  }

  setValueFormBuscar = (value: IFormBuscarPersonas) => {
    const state = this.getState();
    this.setState({ ...state, formBuscar: value });
  };

  resetFormBuscar = () => {
    const state = this.getState();
    this.setState({ ...state, formBuscar: buildNewFormBuscarPersonas() });
  };

  deletePersonBegin = () => {
    this.setState({ ...this.getState(), isLoading: true });
  };

  deletePersonSuccess = () => {
    this.setState({ ...this.getState(), isLoading: false });
  };

  deletePersonError = () => {
    this.setState({ ...this.getState(), isLoading: false });
  };

  //====================================================
  // ACCIONES ASINCRONAS
  //====================================================

  asyncFetchPersons = (
    pageRequest: IDataGridPageRequest = {
      page: this.getState().source.page,
      pageSize: this.getState().source.pageSize,
      orderBy: this.getState().source.orderBy,
      orderDir: this.getState().source.orderDir,
    },
    filters = this.getState().formBuscar) => {
    this.fetchPersonsBegin();
    this.autorizacionService.fetchPersons(pageRequest, filters).subscribe(x => {
      this.fetchPersonsSuccess(
        x.persons,
        x.total,
        pageRequest.page,
        pageRequest.pageSize,
        pageRequest.orderBy,
        pageRequest.orderDir);
    }, (error) => {
      this.fetchPersonsError(error);
    });
  }

  asyncDeletePerson = (id: string) => {
    return new Promise((resolve, reject) => {
      this.deletePersonBegin();
      this.autorizacionService.deletePerson(id).subscribe(() => {
        this.deletePersonSuccess();
        resolve();
      }, error => {
        this.deletePersonError();
      });
    });

  }

}
