import { CrudStore } from './../../../store/crud.store';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import * as moment from 'moment';

@Component({
  selector: 'app-form-buscar-aut-online',
  templateUrl: './form-buscar-aut-online.component.html',
  styleUrls: [ './form-buscar-aut-online.component.scss' ]
})
export class FormBuscarAutorizacionOnlineComponent implements OnInit {


  //readonly state$ = this.store.
  @Output('on-click-nuevo') onClickNuevoEvent: EventEmitter<any> = new EventEmitter();

  form: FormGroup;

  constructor(private formBuilder: FormBuilder, private store: CrudStore) { }

  ngOnInit() {
    this.buildForm();
  }

  buildForm = () => {
    this.form = this.formBuilder.group({
      name: [ null ],
      surname: [ null ],
      email: [ null ],
      birthday: [ null ]
    });
  }

  HandleSubmit = () => {
    this.store.actionGridPersonas.setValueFormBuscar(this.form.value);
    this.store.actionGridPersonas.asyncFetchPersons();
  }

  HandleLimpiar = () => {
    this.form.reset();
    this.store.actionGridPersonas.resetFormBuscar();
    this.store.actionGridPersonas.asyncFetchPersons();
  }

  HandleClickNuevo = () => {
    this.onClickNuevoEvent.emit();
  }

}
