import { FormPersonaComponent } from '../components/forms/form-persona/form-persona.component';
import { Component, OnInit } from '@angular/core';
import { CrudStore } from '../store/crud.store';
import { IDataGridEvent, IDataGridButtonEvent, AlertService, DialogService } from '@siu/shared';

@Component({
  selector: 'app-crud',
  templateUrl: './crud.component.html',
  styleUrls: [ './crud.component.sass' ],
  providers: [
    CrudStore
  ]
})
export class CrudComponent implements OnInit {

  readonly state$ = this.crudStore.state$;

  constructor(private crudStore: CrudStore, public dialog: DialogService, private alert: AlertService) { }

  ngOnInit() {
    this.crudStore.actionGridPersonas.asyncFetchPersons();
  }

  openModalUpdate = (id: string) => {

    this.crudStore.actionModalPersonas.setModalEditar(id);

    const dialogRef = this.dialog.openSM(FormPersonaComponent);

    dialogRef.componentInstance.crudStore = this.crudStore;

    dialogRef.afterClosed().subscribe(() => {
      this.crudStore.actionModalPersonas.resetModalPersona();
    })
  }

  handleClickNuevo = () => {

    const dialogRef = this.dialog.openSM(FormPersonaComponent);

    dialogRef.componentInstance.crudStore = this.crudStore;

    dialogRef.afterClosed().subscribe(() => {
      this.crudStore.actionModalPersonas.resetModalPersona();
    })
  }

  openModalConsultar = (id: string) => {

    this.crudStore.actionModalPersonas.setModalConsultar(id);

    const dialogRef = this.dialog.openSM(FormPersonaComponent, {disableClose: false});

    dialogRef.componentInstance.crudStore = this.crudStore;

    dialogRef.afterClosed().subscribe(() => {
      this.crudStore.actionModalPersonas.resetModalPersona();
    })
  }

  handleLoadData = (e: IDataGridEvent) => {
    console.log(e);
    this.crudStore.actionGridPersonas.asyncFetchPersons({
      page: e.page,
      pageSize: e.pageSize,
      orderBy: e.orderBy,
      orderDir: e.orderDir
    });
  }

  deletePerson = (id) => {
    this.alert.open('Va eliminar el registro seleccionado ¿Continuar?', null, { confirm: true }).then(confirm => {
      if (confirm) {
        this.crudStore.actionGridPersonas.asyncDeletePerson(id).then(() => {
          this.alert.open('Registro eliminado', null, { icon: 'success' });
          this.crudStore.actionGridPersonas.asyncFetchPersons();
        });
      }
    });
  }

  handleClickButton = (e: IDataGridButtonEvent) => {

    switch (e.action) {
      case 'CONSULTAR':
        this.openModalConsultar(e.item._id);
        break;
      case 'EDITAR':
        this.openModalUpdate(e.item._id);
        break;
      case 'ELIMINAR':
        this.deletePerson(e.item._id);
        break;
    }
  }
}
