import { BuildGridButton } from '@siu/shared';
import { IDataGridDefinition, IDataGridSource } from '@siu/shared';
import { IAutorizacion } from '../../../interfaces/autorizacion.interface';
import { FormType } from '@siu/shared';

export const buildNewFormBuscarPersonas = (): IFormBuscarPersonas => ({
  name: null,
  surname: null,
  email: null,
  age: null
});

export const buildNewPersona = (): IAutorizacion => ({
  _id: null,
  guid: null,
  name: null,
  surname: null,
  gender: null,
  email: null,
  phone: null,
  address: null,
  birthday: null,
  age: null
});

export const buildNewModalPersona = (): IModalPersona => ({
  title: 'Registrar Persona',
  error: null,
  isLoading: false,
  type: FormType.REGISTRAR,
  persona: buildNewPersona(),
  idPersona: null
});

export interface IFormBuscarPersonas {
  name: string;
  surname: string;
  email: string;
  age: number;
}

export interface IGridPersonas {
  isLoading: boolean;
  error: any,
  definition: IDataGridDefinition,
  source: IDataGridSource<IAutorizacion>,
  formBuscar: IFormBuscarPersonas
}

export interface IModalPersona {
  title: string;
  isLoading: boolean;
  error: any;
  type: FormType;
  persona: IAutorizacion;
  idPersona?: string;
}

export class CrudStoreModel {

  modalPersona: IModalPersona = buildNewModalPersona();

  gridPersonas: IGridPersonas = {
    formBuscar: buildNewFormBuscarPersonas(),
    isLoading: false,
    error: null,
    definition: {
      columns: [
        { label: 'Nombre', field: 'name' },
        { label: 'Apellidos', field: 'surname' },
        { label: 'Email', field: 'email' },
        { label: 'Age', field: 'age' },
        { label: 'F. Nacimiento', field: 'birthday', isDatetime: true },
        {
          label: 'Acciones', field: 'buttons', buttons: [
            BuildGridButton.CONSULTAR(),
            BuildGridButton.EDITAR(),
            BuildGridButton.ELIMINAR(),
          ]
        }
      ]
    },
    source: {
      items: [],
      page: 1,
      pageSize: 10,
      total: 0,
      orderBy: null,
      orderDir: null
    }
  };

}
