import { FormType } from '@siu/shared';
import { IAutorizacion } from './../../../../interfaces/autorizacion.interface';
import update from 'immutability-helper';
import { IModalPersona, buildNewPersona, buildNewModalPersona } from '../crud.store.model';
import { AutorizacionService } from 'src/app/modules/Autorizaciones/services/autorizacion.service';

export class CrudModalPersonaActions {

  constructor(
    private getState: () => IModalPersona,
    private setState: (newState: IModalPersona) => void,
    public autorizacionService: AutorizacionService) {
  }

  getPersonStart = () => {
    const state = this.getState();
    this.setState({ ...state, isLoading: true, error: null });
  }

  getPersonSuccess = (value: IAutorizacion) => {
    const state = this.getState();
    const newState = update(state, {
      isLoading: { $set: false },
      persona: { $set: value }
    });
    this.setState(newState);
  }

  getPersonError = (error: any) => {
    const state = this.getState();
    const newState = update(state, {
      isLoading: { $set: false },
      error: { $set: error }
    });
    this.setState(newState);
  }

  setValueFormPersona = (newValue: IAutorizacion) => {
    const state = this.getState();

    const newState = update(state, {
      persona: { $set: newValue }
    });

    this.setState(newState);
  }

  resetModalPersona = () => {
    this.setState(buildNewModalPersona());
  }

  savePersonSuccess = () => {
    const state = this.getState();

    this.setState({
      ...state,
      isLoading: false,
      error: null
    });
  }

  savePersonError = (error) => {
    const state = this.getState();

    this.setState({
      ...state,
      isLoading: false,
      error: error
    });
  }

  setModalConsultar = (id: string) => {
    const state = this.getState();
    this.setState({
      ...state,
      title: 'Consultar Persona',
      idPersona: id,
      type: FormType.CONSULTAR
    });
  }

  setModalEditar = (id: string) => {
    const state = this.getState();
    this.setState({
      ...state,
      title: 'Editar Persona',
      idPersona: id,
      type: FormType.EDITAR
    });
  }

  //====================================================
  // ACCIONES ASINCRONAS
  //====================================================

  asyncGetPerson = (
    id: string) => {
    this.getPersonStart();
    this.autorizacionService.getPerson(id).subscribe(x => {
      this.getPersonSuccess(x);
    }, (error) => {
      this.getPersonError(error);
    });
  }

  asyncSavePerson = (person: IAutorizacion) => {
    return new Promise((resolve, reject) => {
      this.getPersonStart();
      this.autorizacionService.savePerson(person).subscribe(
        response => {
          this.savePersonSuccess();
          resolve();
        },
        error => {
          this.savePersonError(error.errors);
        }
      );
    });
  }

}
